@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');




body {
  margin: 0px;
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  font-size: 15px;
}

h1 {
  font-size: 20px;
}

.App {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  background: url('./asset/sunset.jpg') no-repeat center center/cover;
}



.container {
  max-width: 700px;
  max-height: 1400px;
  margin: auto;
  padding: 0 1rem;
  position: relative;

  display: flex;
  flex-direction: column;
}

.bold {
  font-weight: 700;
}

.search {
  padding: 19px;
  display: flex;
  justify-content: center;
  margin-top: -100px;
}

.input {
  padding: 12px;
  border-width: 2px;
  border-radius: 17px;
  border-color: rgb(242, 242, 52);

}

.input::placeholder {
  color: gold;
  margin: 5px;
}

.buttom {
  display: flex;
  margin-top: 90px;
  justify-content: space-between;
  color: rgb(32, 30, 30);
  border: solid;
  background-color: aqua;
  border-color: yellow;
  border-radius: 25px;
  padding: 5px;
}

.location {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.top {
  margin-left: 25%;
  border: solid;
  padding: 15px;
  border-radius: 15px;
  width: 50%;
  border-color: yellow;
  color: black;
  background-color: rgb(191, 104, 104);


}

.logo {
  border: solid;
  display: flex;
  padding: 3px;
  display: inline-block;
  border-radius: 10px;
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;
  background-color: rgb(221, 184, 184);
  color: black;
  margin: 13px;

}

@media(min-width<500px) {
  .top {
    margin-left: 25%;
    padding: 30px;
  }
}

.name {
  font-size: larger;
  margin-top: 5px;
}